* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.container-interval {
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.button {
  width: 70px;
  height: 40px;
  box-shadow: inset -1px -1px rgba(0, 0, 0, .2);
}

.button:hover, .button:focus {
  box-shadow: none;
  box-shadow: inset 1px 1px rgba(0, 0, 0, .3);
}

.button.start {
  width: 80px;
  margin-right: 10px;
}

.timer {
  margin-top: 15px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.field:not(:last-child) {
  margin-right: 15px;
}

.value {
  font-size: 30px;
  line-height: 1.2;
}

.label {
  font-size: 13px;
  line-height: 1.2;
}

